const images = (state = [{name:'',url:'data:image/png;base64,'},{name:'',url:'data:image/png;base64,'},{name:'',url:'data:image/png;base64,'},{name:'',url:'data:image/png;base64,'}], action)=>{
    if (action.type === "SET_IMAGE") {
        state = action.image
    }
    return state;
}

export default images;